/*******************************************************************/
/*                                                                 */
/*                           IMAGES                                */
/*                                                                 */
/*******************************************************************/
img {
  width: 100%;
  height: auto; }

.site-main .img-container img {
  display: block;
  height: 40vw;
  object-fit: cover; }

/*******************************************************************/
/*                                                                 */
/*                       HEADER LAYOUT                             */
/*                                                                 */
/*******************************************************************/
header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding-top: 2rem;
  padding-bottom: 2rem; }
  header:before {
    content: "";
    height: 100%;
    background: -webkit-linear-gradient(top left, #422b5f, #f76e5b);
    background: linear-gradient(to bottom right, #422b5f, #f76e5b);
    position: absolute;
    width: 100vw;
    left: calc(500px - 50vw);
    z-index: -1; }
  header .site-title {
    flex-grow: 0; }
    header .site-title .site-logo {
      font-size: 12px;
      display: inline-block;
      border: 1px solid white;
      border-radius: 50%;
      padding-left: 15px;
      padding-right: 15px;
      padding-top: 22px;
      padding-bottom: 22px;
      margin-right: .5rem;
      vertical-align: super; }
    header .site-title .site-title-h1 {
      display: inline-block;
      font-size: 1.5em; }
  header .header-nav {
    flex-grow: 1; }
    header .header-nav ul {
      list-style: none;
      padding: 0;
      display: flex;
      flex-flow: row nowrap;
      justify-content: center; }
      header .header-nav ul li {
        padding-left: 2%;
        padding-right: 2%; }
  header .search-form {
    flex: 0 0 167px;
    position: relative; }
    header .search-form .search-field {
      box-sizing: content-box;
      border: solid 1px #ccc;
      border-radius: 10em;
      padding-left: 10px;
      padding-right: 25px;
      padding-top: 9px;
      padding-bottom: 9px; }
    header .search-form .search-submit {
      position: absolute;
      right: 5px;
      border: none;
      background-color: transparent;
      background-color: initial;
      top: 10px; }
      header .search-form .search-submit img {
        width: 15px; }

/*******************************************************************/
/* ================= end of header layout ======================== */
/*******************************************************************/
/*******************************************************************/
/*                                                                 */
/*                       MAIN LAYOUT                               */
/*                                                                 */
/*******************************************************************/
.site-main {
  min-height: 65vh; }
  .site-main .section .section-title {
    text-align: center;
    margin-top: 5rem; }
  .site-main .section .img-container {
    position: relative;
    text-align: left; }
    .site-main .section .img-container .img-description {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.8);
      color: white;
      margin-top: 0;
      margin-bottom: 0;
      padding-left: 2rem;
      padding-right: 2rem;
      padding-top: 0.5rem;
      padding-bottom: 0.5rem; }
  .site-main .carousel .section {
    position: relative;
    text-align: center;
    margin-top: 5rem; }
    .site-main .carousel .section .section-title {
      position: absolute;
      left: 0;
      right: 0;
      z-index: 1;
      background: rgba(255, 255, 255, 0.6);
      margin-top: 0;
      padding-top: 1rem;
      padding-bottom: 1rem; }
    .site-main .carousel .section button:focus {
      outline: 0; }
    .site-main .carousel .section button#first, .site-main .carousel .section button#second, .site-main .carousel .section button#third {
      background: rgba(0, 0, 0, 0.5);
      border: 0;
      border-radius: 50%;
      height: 20px;
      width: 20px;
      margin-left: 3px;
      margin-right: 3px;
      margin-top: .5rem; }
      .site-main .carousel .section button#first:focus, .site-main .carousel .section button#second:focus, .site-main .carousel .section button#third:focus {
        background: rgba(0, 0, 0, 0.8); }
    .site-main .carousel .section button#prev, .site-main .carousel .section button#next {
      background: rgba(255, 255, 255, 0.6);
      border: 0;
      border-radius: 50%;
      color: rgba(0, 0, 0, 0.7);
      position: absolute;
      top: 40%;
      font-size: 1.5rem;
      padding-left: 10px;
      padding-right: 10px;
      padding-top: 5px;
      padding-bottom: 5px; }
    .site-main .carousel .section button#prev {
      left: 1rem; }
    .site-main .carousel .section button#next {
      right: 1rem; }
  .site-main .contact-form label {
    display: block;
    margin-top: 1rem;
    margin-bottom: 1rem; }
    .site-main .contact-form label span {
      display: inline-block;
      width: 15%;
      text-align: right;
      padding-right: 1rem;
      box-sizing: border-box; }
  .site-main .contact-form input:not([type="checkbox"]):not([type="submit"]), .site-main .contact-form textarea {
    width: 80%;
    border-radius: 10px;
    border: 1px solid #422b5f;
    padding-left: 0.6rem;
    padding-right: 0.6rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    box-sizing: border-box; }
  .site-main .contact-form textarea {
    min-height: 10rem; }
  .site-main .contact-form input[type="checkbox"], .site-main .contact-form input[type="submit"] {
    margin-left: 15%; }
  .site-main .contact-form input[type="submit"] {
    background-color: transparent;
    background-color: initial;
    border-radius: 10px;
    border: 1px solid #422b5f;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem; }
    .site-main .contact-form input[type="submit"]:hover {
      background-color: #422b5f;
      color: #fff;
      cursor: pointer; }
  .site-main .contact-success-results {
    border: 2px solid #b24257;
    border-radius: 1rem;
    padding-left: 1rem;
    padding-right: 1rem; }

/*******************************************************************/
/* ================= end of main layout ========================== */
/*******************************************************************/
